.search_bar{
    border-bottom: 1px solid #dedede;
}
.search{
    width: 100%;
    background-color: #fff;
    height: 46px;
    border-bottom: 1px solid #dedede;
}

.sub{
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    margin: 0 auto;
}

.sub span{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    line-height: 48px;
    font-weight: 400;
    background-color: #d2cbce80;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 10px 0px 0px 10px;
    cursor: pointer;
}
@media(min-width:375px){
    .sub input[type="text"]{
        height: 40px;
        width: 100%;
        /* border-right: 1px solid #00000040; */
        padding-left: 30px;
        padding-right: 30px;
        border-radius: 0;
        padding: 0 30px;
        /* height: 46px; */
        box-sizing: border-box;
        border: none;
    } 
}
@media(min-width:768px){
    .sub input[type="text"]{
        font-size: medium;
        height: 40px;
        width: 100%;
        /* border-right: 1px solid #00000040; */
        padding-left: 30px;
        padding-right: 30px;
        border-radius: 0;
        padding: 0 30px;
        /* height: 46px; */
        box-sizing: border-box;
        border: none;
    }   
}
@media(min-width:1024px){
    .sub input[type="text"]{
        height: 40px;
        width: 100%;
        /* border-right: 1px solid #00000040; */
        padding-left: 30px;
        padding-right: 30px;
        border-radius: 0;
        padding: 0 30px;
        /* height: 46px; */
        box-sizing: border-box;
        border: none;
    } 
}

.search-animation-container{
    position: absolute;
    display: block;
    box-sizing: content-box;
    width:100%;
    z-index: 10002;
    background-color: white;
}
.search-animation-container h3{
    padding-left: 20px;
    margin-bottom: 0px;
}
.search_active{
    display: block; 
}
.search_hide{
    display: none;
}
.search-list-container{
    width: 100%;
}
.search-list-container ul{
    list-style: none;
    display: inline-block;
    width: 100%;
    padding: 0px ;
}
.search-list-container ul li{
    border-bottom: 1px solid #8080805e;
    line-height: 50px;
    padding-left: 20px;
    cursor: pointer;
}
.search_results_container{
    padding:10px;
}